import React from 'react'
import Pdf from './files/CharlesCurtisResume.pdf'

const Resume = () => {
  return (
    <div className="resume p-2">
      <a className="btn btn-success" href={Pdf}>View/Download Resume</a>
      <br />
      <div className="icons mt-4">
        <i className="devicon-atom-original-wordmark m-5"></i>
        <i className="devicon-bootstrap-plain-wordmark m-5"></i>
        <i className="devicon-css3-plain-wordmark m-5"></i>
        <br />
        <i className="devicon-express-original m-5"></i>
        <i className="devicon-git-plain-wordmark m-5"></i>
        <i className="devicon-github-plain m-5"></i>
        <br />
        <i className="devicon-heroku-plain-wordmark m-5"></i>
        <i className="devicon-html5-plain-wordmark m-5"></i>
        <i className="devicon-javascript-plain m-5"></i>
        <br />
        <i className="devicon-jquery-plain-wordmark m-5"></i>
        <i className="devicon-mongodb-plain-wordmark m-5"></i>
        <i className="devicon-nodejs-plain-wordmark m-5"></i>
        <br />
        <i className="devicon-rails-plain-wordmark m-5"></i>
        <i className="devicon-react-original m-5"></i>
        <i className="devicon-ruby-plain m-5"></i>
      </div>
    </div>
  )
}

export default Resume



