import React from 'react'

const About = () => {
  return (
    <div className="about">
      <p className="p-3">
        Hi!  My name is Charles Curtis and I&apos;m a Full Stack Web Developer.  I have exprence using HTML, CSS, JavaScript, Bootstrap, Handlebars, React.js, Ruby on Rails, MySQL, Express.js, and MongoDB.  To view my projects, please click the &apos;Projects&apos; button above!
        <br />
        <br />
        I decided to become a Web Developer because I enjoy the process finding creative
        ways to solve problems.  Whether it&apos;s figuring out the best way to make that new
        feature work or learning which new technology is best suited for our project, the process
        of learning and growing is a one that I&apos;ve come to love.
      </p>
    </div>
  )
}

export default About



