import React, { Component } from 'react'
import Carousel from 'react-bootstrap/Carousel'

class Projects extends Component {
  render () {
    return (
      <div className="projects">
        <Carousel>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="https://i.imgur.com/LKqK3Qe.jpg"
              alt="Expense Tracker"
            />
            <Carousel.Caption className="p-0 mb-0 carousel-caption">
              <h3>Expense Tracker</h3>
              <p className="m-3">This app was created for users to track personal spending.  The app is built using React.js and uses Ruby on Rails for the backend.</p>
              <a className="btn btn-light mx-1" onClick={() => window.open('https://cpcurtis1218.github.io/capstone-client', '_blank')}>Visit Site</a>
              <a className="btn btn-light mx-1" onClick={() => window.open('https://github.com/cpcurtis1218/capstone-client', '_blank')}>View Repo</a>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="https://i.imgur.com/TuWXIsQ.png"
              alt="Nile E-Commerce"
            />

            <Carousel.Caption className="p-0 mb-0 carousel-caption">
              <h3>Nile</h3>
              <p className="m-3">Nile is an E-Commerce site that was built as a group project. The app was built using HTML, CSS, and JavaScript on the
              front-end and Express.js with MongoDB on the back-end.</p>
              <a className="btn btn-light mx-1" onClick={() => window.open('https://wdi-3-0.github.io/nile-client/', '_blank')}>Visit Site</a>
              <a className="btn btn-light mx-1" onClick={() => window.open('https://github.com/wdi-3-0/nile-client', '_blank')}>View Repo</a>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="https://i.imgur.com/pDBBlik.png"
              alt="Wanderlust"
            />

            <Carousel.Caption className="p-0 mb-0 carousel-caption">
              <h3>Wanderlust</h3>
              <p className="m-3">Wanderlust was built to help users keep track of travel destinations. Wanderlust uses HTML, CSS, and JavaScript on the
              front-end and Ruby on Rails on the back-end. This was my first full-stack application.</p>
              <a className="btn btn-light mx-1" onClick={() => window.open('https://cpcurtis1218.github.io/Wanderlust-Client/', '_blank')}>Visit Site</a>
              <a className="btn btn-light mx-1" onClick={() => window.open('https://github.com/cpcurtis1218/Wanderlust-Client', '_blank')}>View Repo</a>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="https://i.imgur.com/oPXYp6z.png"
              alt="Tic-Tac-Snow"
            />

            <Carousel.Caption className="p-0 mb-0 carousel-caption">
              <h3>Tic-Tac-Snow</h3>
              <p className="m-3">Tic-Tac-Snow is a browser-based tic-tac-toe game.  The app uses HTML, CSS, and JavaScript for the client.  This was my first
              front-end application.</p>
              <a className="btn btn-light mx-1" onClick={() => window.open('https://cpcurtis1218.github.io/tic-tac-toe-client/', '_blank')}>Visit Site</a>
              <a className="btn btn-light mx-1" onClick={() => window.open('https://github.com/cpcurtis1218/tic-tac-toe-client', '_blank')}>View Repo</a>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      </div>
    )
  }
}

export default Projects



